"use client";
import { useState } from "react";
import { FaSearch, FaPlus } from "react-icons/fa";

const usersMock = [
  { id: "user001", name: "Mariiya Zajaceva", status: "admin" },
  { id: "user002", name: "Karsiaryna Dabreha", status: "vip" },
  { id: "user003", name: "Pablo Torres", status: "standard" },
  { id: "user004", name: "Lola Diaz", status: "premium" },
  { id: "user005", name: "Alex Roman", status: "user" },
  { id: "user006", name: "David Ortega", status: "vip" },
];

export default function UserSidebar() {
  const [search, setSearch] = useState("");

  const filteredUsers = usersMock.filter((u) =>
    u.name.toLowerCase().includes(search.toLowerCase())
  );

  return (
    <div className="flex flex-col gap-4">
      <div className="relative">
        <input
          type="text"
          placeholder="Search"
          value={search}
          onChange={(e) => setSearch(e.target.value)}
          className="w-full pl-10 pr-4 py-2 bg-gray-100 border border-gray-300 rounded-md focus:outline-none"
        />
        <FaSearch className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-500" />
      </div>


      <button className="w-full flex items-center justify-center gap-2 bg-[#2ED3C3] text-white py-2 rounded-md hover:bg-[#26bcb0]">
        <FaPlus />
        Add user
      </button>

      <ul className="flex flex-col gap-2 mt-2">
        {filteredUsers.map((user) => (
          <li
            key={user.id}
            className="p-2 border rounded-md flex justify-between items-center hover:bg-gray-50"
          >
            <span className="font-medium">{user.name}</span>
            <span className="text-xs text-gray-500 uppercase">{user.status}</span>
          </li>
        ))}
      </ul>
    </div>
  );
}
